import React from 'react';
import PropTypes from 'prop-types';

import APInlineButton from './APInlineButton/APInlineButton';
import { styleNames } from '../../propTypes/commonComponent';

const DownloadAPButton = props => {
  return (
    <APInlineButton styleName={styleNames.general} icon="download" {...props} />
  );
};

DownloadAPButton.propTypes = {
  text: PropTypes.string
};

DownloadAPButton.defaultProps = {
  text: 'Download'
};

export default DownloadAPButton;



