import React from 'react';
import PropTypes from 'prop-types';

import APInlineButton from './APInlineButton/APInlineButton';
import { styleNames } from '../../propTypes/commonComponent';

const ExecuteAPButton = props => {
  return (
    <APInlineButton
      styleName={styleNames.additive}
      icon="check"
      iconModifier="success"
      {...props}
    />
  );
};

ExecuteAPButton.propTypes = {
  text: PropTypes.string
};

ExecuteAPButton.defaultProps = {
  text: 'Execute'
};

export default ExecuteAPButton;



