import React from 'react';
import PropTypes from 'prop-types';

import APInlineButton from './APInlineButton/APInlineButton';
import { styleNames } from '../../propTypes/commonComponent';

const CancelAPButton = props => {
  return (
    <APInlineButton
      styleName={styleNames.subtractive}
      icon="close-inverse"
      iconModifier="danger"
      {...props}
    />
  );
};

CancelAPButton.propTypes = {
  text: PropTypes.string,
  textClass: PropTypes.string
};

CancelAPButton.defaultProps = {
  text: 'Cancel'
};

export default CancelAPButton;



